@import url(https://fonts.googleapis.com/css2?family=Luckiest+Guy&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
.main-page__content {
  min-width: 650px;
  padding: 42px calc(50% - 500px);
  display: flex;
  justify-content: space-between;
}

.main-page__main-section {
  padding-left: 40px;
  box-sizing: border-box;
  width: 60%;
}

.main-page__search-box {
  margin-bottom: 35px;
}

.main-page__favorites {
  width: 35%;
}

/* ---------------------------------------------------- */

@media (min-width: 320px) and (max-width: 768px) {
  .main-page__favorites {
    width: 20%;
    height: 30%;
    display: flex;
    justify-content: center;
  }

  .main-page__main-section {
    padding-left: 20px;
    box-sizing: border-box;
    width: 100%;
    min-width: 220px;
    max-width: 600px;
  }

  .main-page__content {
    max-width: 600px;
    width: 100%;
    margin: 0 auto;
    min-width: 220px;
    display: flex;
    justify-content: space-between;
  }
}

.header {
  background: #496ddb;
  padding: 20px calc(50% - 500px) 10px;
}

.header__title {
  font-family: "Luckiest Guy", cursive;
  color: #ffeedb;
  font-size: 45px;
  margin-left: 40px;
}

.search-box__form {
  display: flex;
}

.search-box__form-label {
  display: inline-block;
  font-size: 12px;
  margin-right: 10px;
}

.search-box__form-input {
  box-sizing: border-box;
  display: block;
  width: 300px;
  margin-top: 5px;
  font-size: 14px;
  padding: 5px 10px;
  border: 1px solid rgba(0, 8, 7, 0.5);
}

.search-box__form-submit {
  margin-top: 17px;
  padding: 5px 10px;
  border: none;
  transition: all 0.2s linear;
  color: #ffeedb;
  background-color: rgba(0, 8, 7);
}

.search-box__form-submit:hover:not(:disabled) {
  opacity: 0.8;
  cursor: pointer;
  transition: all 0.2s linear;
  background-color: #ffeedb;
  color: rgba(0, 8, 7);
}

.search-box__form-submit:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

@media (min-width: 320px) and (max-width: 768px) {
  .search-box__form-label {
    display: inline-block;
    font-size: 12px;
    margin-right: 10px;
    min-width: 190px;
    max-width: 370px;
    width: 70%;
  }

  .search-box__form {
    flex-wrap: wrap;
    display: flex;
    width: 100%;
    min-width: 200px;
    max-width: 400px;
  }

  .search-box {
    min-width: 320px;
    max-width: 768px;
    width: 100%;
  }

  .search-box__form-input {
    box-sizing: border-box;
    display: block;
    min-width: 180px;
    width: 100%;
    max-width: 400px;
    margin-top: 5px;
    font-size: 14px;
    padding: 5px 10px;
    border: 1px solid rgba(0, 8, 7, 0.5);
  }

  .search-box__form-submit {
    text-align: center;
    margin-top: 17px;
    padding: 5px;
    min-width: 55px;
    width: 20%;
    max-width: 65px;
    border: none;
    transition: all 0.2s linear;
    color: #ffeedb;
    background-color: rgba(0, 8, 7);
  }
}

.movie-item {
  padding: 20px;
  background-color: #ffeedb;
  display: flex;
  min-width: 300px;
  min-height: 150px;
}

.movie-item__poster {
  height: 150px;
  margin-right: 21px;
}

.movie-item__title {
  font-weight: 500;
  font-size: 21px;
  margin-bottom: 14px;
}

.movie-item__add-button {
  line-height: 1;
  width: 140px;
  height: 30px;
  border: none;
  background-color: #496ddb;
  padding: 3px 12px;
  color: #ffeedb;
  font-size: 12px;
}
.movie-item__add-button:hover {
  cursor: pointer;
  opacity: 0.9;
  transition: all 1s linear;
}

.not-poster {
  border: 1px solid;
  text-align: center;
  padding: 5px;
  background-color: white;
  width: 100px;
  height: 150px;
  margin-right: 21px;
  align-items: center;
  display: flex;
}

.movie-item__add-button:disabled {
  background-color: rgb(48, 163, 37);
  color: rgb(255, 255, 255);
  border-radius: 5px;
  cursor: not-allowed;
  width: 100px;
  height: 30px;
}
.movie-item__btn-delete {
  cursor: pointer;
  color: white;
  background-color: rgb(243, 67, 35);
  border-radius: 5px;
  border: none;
  margin-top: 5px;
  font-size: 12px;
  height: 30px;
  width: 130px;
}
.box-btn-add-delete {
  flex-direction: column;
  display: flex;
}

@media (min-width: 320px) and (max-width: 768px) {
  .movie-item__add-button {
    padding: 0;
  }
  .movie-item {
    margin-right: 10px;
    padding: 5px;
    background-color: #ffeedb;
    display: flex;
    min-width: 250px;
    width: 100%;
    max-width: 500px;
    min-height: 100px;
  }
  .movie-item__poster {
    min-width: 60px;
    width: 90%;
    max-width: 75px;

    min-height: 100px;
    height: 90%;
    max-height: 150px;
    margin-right: 11px;
  }
  .movie-item__title {
    min-width: 140px;
    width: 100%;
    max-width: 300px;
    font-weight: 400;
    font-size: 18px;
    margin-bottom: 10px;
  }
  .movie-item__info {
    width: 100%;
    min-width: 140px;
    max-width: 500px;
  }
}

.movies__item {
  margin-bottom: 21px;
}

@media (min-width: 320px) and (max-width: 768px) {
  .movies__item {
    margin-bottom: 10px;
    min-width: 250px;
    max-width: 500px;
    width: 100%;
  }
  .movies {
    min-width: 240px;
    width: 100%;
    max-width: 600px;
  }
}

.favorites {
  box-sizing: border-box;
  border: 1px solid rgba(0, 8, 7);
  padding: 30px 20px;
  position: fixed;
  max-width: 400px;
  width: 30%;
  min-width: 300px;
}

.favorites__name {
  box-sizing: border-box;
  width: 100%;
  font-size: 16px;
  padding: 5px 10px;
  border: 1px solid rgba(0, 8, 7, 0.5);
  margin-bottom: 24px;
}

.favorites__list {
  margin-bottom: 24px;
}

.favorites__save {
  border: none;
  background-color: #496ddb;
  padding: 10px 15px;
  color: #ffeedb;
  font-size: 14px;
  transition: all 0.2s linear;
}

.favorites__save:hover:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}
.favorites__save:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}
.favorites__save:hover {
  cursor: pointer;
  opacity: 0.9;
  transition: all 0.2s linear;
}

.btn-delete {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 26px;
  width: 26px;
  height: 22px;
  cursor: pointer;
  border-radius: 8px;
  border: solid rgb(204, 202, 202) 1px;
}

.btn-delete:disabled:hover {
  opacity: 0.5;
  background-color: rgb(0, 0, 0);
}

.btn-delete:hover {
  background-color: rgb(253, 0, 0);
  color: cornsilk;
}

.point-favorites {
  margin-bottom: 10px;
  align-items: center;
  justify-content: space-between;
  display: flex;
}

.show {
  width: 40px;
  height: 60px;
  opacity: 0.8;
  font-size: 20px;
  background-color: rgb(255, 255, 255);
  border-radius: 10px;
  border: solid 1px rgb(41, 105, 207);
  color: rgb(41, 105, 207);
  right: 3px;
  top: 170px;
  position: fixed;
  display: block;
}
.hide {
  display: none;
}

.mobile_favorites {
  box-sizing: border-box;
  border: 1px solid rgba(0, 8, 7);
  padding: 15px 7px;

  position: fixed;
  max-width: 500px;
  background-color: aliceblue;
  top: 150px;
  width: 80vw;
  left: 40px;
  min-width: 80px;
  max-width: 550px;
}

.mobile_favorites__name {
  box-sizing: border-box;
  width: 90%;
  font-size: 16px;
  padding: 3px 8px;
  border: 1px solid rgba(0, 8, 7, 0.5);
  margin-bottom: 20px;
}

.mobile_favorites__list {
  margin-bottom: 20px;
}

.mobile_favorites__save {
  border: none;
  background-color: #496ddb;
  padding: 10px 15px;
  color: #ffeedb;
  font-size: 14px;
  transition: all 0.2s linear;
}

.mobile_favorites__save:hover:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}
.mobile_favorites__save:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}
.mobile_favorites__save:hover {
  cursor: pointer;
  opacity: 0.9;
  transition: all 0.2s linear;
}

.mobile_favorites_btn_delete {
  display: block;
  min-width: 22px;
  width: 22px;
  min-height: 22px;
  height: 22px;
  cursor: pointer;
  border-radius: 30px;
  border: solid rgb(204, 202, 202) 1px;
}

.mobile_favorites_btn_delete:disabled:hover {
  opacity: 0.5;
  background-color: rgb(0, 0, 0);
}

.mobile_favorites_btn_delete:hover {
  background-color: rgb(253, 0, 0);
  color: cornsilk;
}

.point_mobile_favorites {
  margin-bottom: 7px;
  align-items: center;
  justify-content: space-between;
  display: flex;
}

.list-page {
  padding: 42px calc(50% - 300px);
}

.list-page__title {
  text-align: center;
  font-size: 34px;
  margin-bottom: 26px;
}

.list-page ul li {
  font-size: 18px;
}
.point-list {
  border: 1px solid grey;
  padding: 10px;
  background-color: blanchedalmond;
  display: flex;
  align-items: center;
  margin-top: 10px;
}
.img-list-movies {
  margin-right: 10px;
  height: 100px;
  width: 75px;
}

.not-poster-list-movies {
  height: 100px;
  width: 80px;
  background-color: white;
  font-size: 12px;

  margin-right: 10px;
  border: 1px solid;
  text-align: center;
  padding: 2px;
  align-items: center;
  display: flex;
}

@media (min-width: 320px) and (max-width: 768px) {
  .list-page {
    padding: 20px calc(50% - 300px);
    margin-left: 10px;
    margin-right: 10px;
  }

  .list-page__title {
    margin-top: 20px;
    margin-bottom: 15px;
  }
}

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

/* Color palette https://coolors.co/ffeedb-e63946-6a8d73-000807-496ddb */

body,
button,
input {
  font-family: "Roboto", "Helvetica", sans-serif;
  color: #000807;
}

@media (min-width: 320px) and (max-width: 768px) {
  html {
    min-width: 320px;
    max-width: 768px;
  }
}

